.commonLoginBg-box {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.commonLoginBg-box h1,
.commonLoginBg-box p {
  margin: 0;
  padding: 0;
}
.commonLoginBg-box .left-top-logo {
  position: absolute;
  left: -2%;
  top: -6%;
  z-index: 1;
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
  width: 17.4vw;
}
.commonLoginBg-box .commonLoginBg-cnt {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.commonLoginBg-box .commonLoginBg-cnt .common-title {
  position: relative;
  top: 10%;
  text-align: center;
  font-family: PingFangSC-Light, PingFang SC;
}
.commonLoginBg-box .commonLoginBg-cnt .common-title h1 {
  font-size: 64px;
  line-height: 90px;
}
.commonLoginBg-box .commonLoginBg-cnt .common-title p {
  font-size: 34px;
  line-height: 48px;
}
.commonLoginBg-box .commonLoginBg-cnt .form-box {
  margin-top: 5%;
  width: 23.75vw;
  padding: 30px;
  background: #ffffff;
  box-shadow: 0px 0px 16px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.commonLoginBg-box .commonLoginBg-cnt .login-type {
  text-align: center;
}
.commonLoginBg-box .commonLoginBg {
  position: absolute;
  left: -10%;
  top: -35%;
  width: 2000px;
  height: 1200px;
  background: -webkit-gradient(linear, left top, right top, color-stop(3%, #eee), color-stop(0, transparent)), -webkit-gradient(linear, left top, left bottom, color-stop(3%, #eee), color-stop(0, transparent));
  background: linear-gradient(90deg, #eee 3%, transparent 0), linear-gradient(#eee 3%, transparent 0);
  background-size: 80px 80px;
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
}
